<template>
    <ssh-terminal-tab :url="wsurl"></ssh-terminal-tab>
</template>

<script>
import axios from 'axios';
export default {
    mounted() {
        this.fetchServerDetail();
    },
    methods: {
        fetchServerDetail() {
            axios.get('/api/v1/ssh-servers/' + this.$route.params.token).then(rp => {
                var response = rp.data;
                if (response && response.success) {
                    document.title = response.data;
                }
            });
        }
    },
    computed: {
        wsurl() {
            var token = this.$route.params.token;
            return `ws://127.0.0.1:8199/ws/${token}`;
        },
    },
    beforeRouteLeave(to, from , next) {
      const answer = window.confirm('Do you really want to leave? The ssh connection will be closed.');
      if (answer) {
          next()
      } else {
          next(false)
      }
    }
};
</script>