<template>
    <v-dialog
          :value="dialog"
          persistent
          max-width="500px"
        >
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="12"
                    md="12"
                  >
                    <v-text-field
                      v-model="editedItem.name"
                      label="Server name"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.host"
                      label="IP"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      type="number"
                      v-model="editedItem.port"
                      label="Port"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.user"
                      label="User"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                        :type="displayPassword ? 'text' : 'password'"
                        :append-icon="displayPassword ? 'mdi-eye' : 'mdi-eye-off'"
                        @click:append="displayPassword = !displayPassword"
                        v-model="editedItem.password"
                        label="Password"
                    ></v-text-field>
                  </v-col>

                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.key"
                      label="Key"
                    ></v-text-field>
                  </v-col>

                   <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.ip"
                      label="Description"
                    ></v-text-field>
                  </v-col>
                </v-row>

                <v-alert v-if="!!tip" :type="tip">
                    {{ message }}
                </v-alert>
                
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="close(null)"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
      </v-dialog>
</template>

<script>
import axios from "axios";
export default {
    props: {
        dialog: {
            type: Boolean,
            default: false
        },
        newMode: {
            type: Boolean,
            default: false
        },
        item: {
            type: Object,
            default: null,
        },
        api: {
            type: String,
            default: "",
        }
    },

   data () {
      return {
        tip: "",
        message: "",
        displayPassword: false,
        editedItem: this.item
      }
    },

    methods: {
      close (item) {
        this.$emit('close', item);

        this.$nextTick(() => {
          this.editedItem = {}
        })
      },

      save () {
        var api = this.newMode ? this.api : this.api + '/' + this.editedItem.id;
        axios.post(api, this.editedItem)
          .then(response => {
            this.close(response.data)
          })
          .catch(error => {
            this.tip = "error";
            this.message = error.response.data.message;
          });
      },
    },
    computed: {
      formTitle () {
        return this.newMode ? 'New Item' : 'Edit Item'
      },
    },
    watch: {
      item(n) {
        this.editedItem = n;
      }
    }
  }
</script>
