<template>
  <div style="height: 100%" id="terminal" />
</template>

<script>
import { Base64 } from "js-base64";
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
import { WebLinksAddon } from "xterm-addon-web-links";
import { SearchAddon } from "xterm-addon-search";
import "xterm/css/xterm.css";

const terminal = new Terminal();
// Load WebLinksAddon on terminal, this is all that's needed to get web links
// working in the terminal.
terminal.loadAddon(new WebLinksAddon());

const defaultTheme = {
  foreground: "#ffffff", // 字体
  background: "#1b212f", // 背景色
  cursor: "#ffffff", // 设置光标
  selection: "rgba(255, 255, 255, 0.3)",
  black: "#000000",
  brightBlack: "#808080",
  red: "#ce2f2b",
  brightRed: "#f44a47",
  green: "#00b976",
  brightGreen: "#05d289",
  yellow: "#e0d500",
  brightYellow: "#f4f628",
  magenta: "#bd37bc",
  brightMagenta: "#d86cd8",
  blue: "#1d6fca",
  brightBlue: "#358bed",
  cyan: "#00a8cf",
  brightCyan: "#19b8dd",
  white: "#e5e5e5",
  brightWhite: "#ffffff",
};

export default {
  props: {
    url: String,
    bidirectional: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      isFullScreen: false,
      searchKey: "",
      ws: null,
      term: null,

      fitAddon: new FitAddon(),
      webLinkAddon: new WebLinksAddon(),
      searchAddon: new SearchAddon(),

      rows: 35,
      cols: 100,
      heartBeatTimer: null,
      messageBuffer: null,
      bufferedTime: -1,
    };
  },

  mounted() {
    this.initTerm();
  },

  methods: {
    onTermResize(size) {
      this.rows = size.rows;
      this.cols = size.cols;
      console.log("resize", size);
      if (this.ws) {
        console.log("resize1", size);
        this.ws.send(
          JSON.stringify({
            type: "resize",
            rows: size.rows,
            cols: size.cols,
          })
        );
      }
    },

    onWindowResize() {
      console.log("onWindowResize");
      this.fitAddon.fit(); // it will make terminal resized.
    },

    startHeartBeat() {
      this.heartBeatTimer = setInterval(() => {
        if (this.ws) {
          console.log('heartbeat')
          this.ws.send(
            JSON.stringify({
              type: "hbt",
              cmd: "",
            })
          );
        }
      }, 30 * 1000);
    },

    handleWebSocketMessage(ev) {
      if (this.bufferedTime && this.bufferedTime > 0) {
        if (this.messageBuffer) {
          this.messageBuffer += ev.data;
        } else {
          this.messageBuffer = ev.data;
          setTimeout(() => {
            this.term.write(this.messageBuffer);
          }, this.bufferedTime);
        }
      } else {
        this.term.write(ev.data);
      }
    },

    handleTerminalData(data) {
      if (this.term.socket) {
        this.ws.send(
          JSON.stringify({
            type: "cmd",
            cmd: Base64.encode(data), // encode data as base64 format
          })
        );
      }
    },

    doClose() {
      window.removeEventListener("resize", this.onWindowResize);
      if (this.ws) {
        this.ws.close();
      }
      if (this.term) {
        this.term.dispose();
      }
    },

    termOnWsClose() {
      this.term.setOption("cursorBlink", false);

      delete this.term.socket;
      clearInterval(this.heartBeatTimer);
    },

    initTerm() {
      this.term = new Terminal({
        rendererType: "canvas", // 渲染类型
        rows: this.rows,
        cols: this.cols,
        convertEol: true, // 启用时，光标将设置为下一行的开头
        scrollback: 10, // 终端中的回滚量
        disableStdin: false, // 是否应禁用输入
        fontSize: 18,
        cursorBlink: true, // 光标闪烁
        cursorStyle: "bar", // 光标样式 underline
        bellStyle: "sound",
        theme: defaultTheme,
      });
      this.term.loadAddon(this.webLinkAddon);
      this.term.loadAddon(this.searchAddon);
      this.term.loadAddon(this.fitAddon);

      this.term._initialized = true;
      this.term.prompt = () => {
        this.term.write("\r\n");
      };

      this.term.writeln("Welcome to \x1B[1;3;31mxterm.js\x1B[0m");
      this.term.writeln(
        "This is a local terminal emulation, without a real terminal in the back-end."
      );
      this.term.writeln("Type some keys and commands to play around.");
      this.term.prompt();

      this.term.open(document.getElementById("terminal"));
      this.term.onResize(this.onTermResize);
      this.term.onSelectionChange(() => {
        if (this.term.hasSelection()) {
          navigator.clipboard.writeText(this.term.getSelection());
        }
      });
      window.addEventListener("resize", this.onWindowResize);
      this.fitAddon.fit(); // first resizing
      this.initWs();

      this.term.socket = this.ws;
      if (this.bidirectional) {
        this.term.onData(this.handleTerminalData);
      }
    },

    initWs() {
      this.ws = new WebSocket(
        `${this.url}?cols=${this.cols}&rows=${this.rows}`
      );
      this.ws.onmessage = this.handleWebSocketMessage;
      this.startHeartBeat();
      this.ws.onerror = () => {
        this.$message.error("ws has no token, please login first");
        this.$router.push({ name: "login" });
      };

      this.ws.onclose = () => {
        this.ws.removeEventListener("message", this.handleWebSocketMessage);
        this.term.writeln("\x1B[1;3;31mConnection lost. Please close the page and reconnect it.\x1B[0m");
        this.term.prompt();
        this.termOnWsClose();
      };
    },
  },

  beforeDestroy() {
    console.log('beforeDestroy')
    this.doClose();
  },
};
</script>