<template>
  <div>
     <v-toolbar dense>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn  v-bind="attrs" v-on="on" @click.stop="editItem({})">
              <v-icon>mdi-server-plus mdi-rotate-90</v-icon>
            </v-btn>
          </template>
          <span>Add Server</span>
        </v-tooltip>

        <v-spacer></v-spacer>
      </v-toolbar>

      <ssh-server-edit-dialog 
        :api="api"
        :item="editedItem" 
        :dialog="dialogEdit"
        :new-mode="editedIndex === -1"
        @close="closeDialog"></ssh-server-edit-dialog>

      <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
          <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
            <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-data-table
        :headers="headers"
        :items="servers"
        class="elevation-1"
        :search="search"
        :items-per-page="-1"
        >
        <template v-slot:top>
          <v-text-field
            v-model="search"
            label="Search"
            class="mx-4"
          ></v-text-field>
        </template>

        <template v-slot:[`item.actions`]="{ item }">
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            mdi-pencil
          </v-icon>
          
          <v-icon
            small
            class="mr-2"
            @click="sshconnect(item)"
          >
            mdi-connection
          </v-icon>

          <router-link
              :to="{ name: 'system.sshterminal', params: { token: item.id }}"
              target="_blank"> Connect
          </router-link>
        </template>
        <template v-slot:no-data>
          Data not found
        </template>
      </v-data-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
   data () {
      return {
        api: "/api/v1/ssh-servers",
        dialogEdit: false,
        dialogDelete: false,
        displayPassword: false,

        search: '',
        servers: [],
        headers: [
          { text: 'Name', align: 'start', sortable: true, value: 'name', },
          { text: 'Host', align: 'start', sortable: true, value: 'host'},
          { text: 'Port', align: 'start', sortable: true, value: 'port',},
          { text: 'Description', align: 'start', sortable: true, value: 'ip' },
          { text: 'Actions', value: 'actions', sortable: false },
        ],
        editedIndex: -1,
        editedItem: {
          id      : 0,          
          name    : "",
          host    : "",
          ip      : "",
          port    : "",
          user    : "",
          key     : "",
          type    : ""
        },
        defaultItem: {
          id      : 0,          
          name    : "",
          host    : "",
          ip      : "",
          port    : "",
          user    : "",
          key     : "",
          type    : ""
        },
      }
    },
    mounted() {
      this.navByRoute();
    },
    methods: {
      navByRoute() {
        axios.get(this.api).then(rp => {
          var response = rp.data;
          if (response && response.success) {
              this.servers = response.data;
          }
        });
      },

      sshconnect(item) {
        console.log('connect', item)
        this.$router.push({ name: 'system.sshterminal', params: { token: item.id } });
      },

      editItem (item) {
        this.displayPassword = false;
        this.editedIndex = this.servers.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogEdit = true
      },

      deleteItem (item) {
        this.editedIndex = this.servers.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        axios.delete(this.api + '/' + this.editedItem.id).then(rp => {
          var response = rp.data;
          console.log('response', response);
        });
        this.closeDelete()
      },

      closeDialog () {
        this.dialogEdit = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },
    watch: {
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

  }
</script>
