import Vue from "vue";
import sshterm_asm from "./sshterminal/_asm.js";

var routes = [];
var navs = [];

const asms = [sshterm_asm];

console.log('asm.js')
for (var i = 0; i < asms.length; i++) {
  const asm = asms[i];
  if (asm.routes) {
    routes = routes.concat(asm.routes);
  }
  if (asm.nav) {
    navs = navs.concat(asm.nav);
  }
  if (asm.components) {
    for (const [name, component] of Object.entries(asm.components)) {
      Vue.component(name, component);
    }
  }
}

export default { routes: routes, navs: navs }


